  agencyForm: FormGroup;
  contactAutGroup: FormGroup;  
  ContactAutPhone = new ContactAutPhoneModel;
  ContactAutEmail = new ContactAutEmailModel;
  AgencyPhone = new AgencyPhoneModel;
  AgencyEmail = new AgencyEmailModel;
  ContactAut = new ContactAutModel();
  Carrier = new CarrierModel;
  OwnerOfficer = new OwnerOfficerModel;
  Status = new StatusModel;
  Agency = new AgencyModel();
  AgencySend = new AgencySendModel();
  AgencyAdd = new AgencyModel();
  
  ContactAutModelForm = this.AgencyModelForm.AbcontAut;
  ContactAutPhoneModelForm = this.AgencyModelForm.AbcontAut;
  ContactAutEmailModelForm = this.AgencyModelForm.AbcontAut;
  AgencyEmailModelForm = this.AgencyModelForm.Abemail;
  AgencyPhoneModelForm = this.AgencyModelForm.Abphone;  
  CarrierModelForm = this.AgencyModelForm.Abcarrier;  
