import { Component } from '@angular/core';
import { CatlogsGetService } from '../_services/CatlogsGet.service';

import { AuthenticationService } from '../_services';
import { UserModel } from '../_models/users-model-data';

import { Router } from '@angular/router';

@Component({
  selector: 'app-nav-menu',
  templateUrl: './nav-menu.component.html',
  styleUrls: ['./nav-menu.component.css']
})

export class NavMenuComponent {
    currentUser: UserModel;
    constructor(      
      private CatlogsGetService: CatlogsGetService,      
      private router: Router,
      private authenticationService: AuthenticationService
    ) {          
      this.authenticationService.currentUser.subscribe(x => this.currentUser = x);
    }  

    public Catlogs: any = [];
    public CatlogsProductWritten: any = [];
    public CatlogsTitle: any = [];
    public CatlogsAgencyStatus: any = [];
    public CatlogsAgencyType: any = [];
    public CatlogsPermissions: any = [];
    public CatlogsPermissionsCRUD: any = [];
    public ObjcurrentUser: UserModel = JSON.parse(localStorage.getItem('currentUser')).Data;

    ngOnInit() {            
      this.CatlogsGetService.getCatlogs().subscribe(res => {
        
        for (let element of Object(res)) {  

          if (element.CatlogsType == 'ProductWritten') {
            this.CatlogsProductWritten.push(element);          
          }
          if (element.CatlogsType == 'Title') {
            this.CatlogsTitle.push(element);
          }
          if (element.CatlogsType == 'AgencyStatus') {
            this.CatlogsAgencyStatus.push(element);
          }
          if (element.CatlogsType == 'AgencyType') {
            this.CatlogsAgencyType.push(element);
          }
          if (element.CatlogsType == 'Permissions') {                        
            for (let elementProfile of this.ObjcurrentUser.Abprofile) {
              for (let elementPermissions of elementProfile.Abpermissions) {                
                if(elementPermissions.CatlogsId==element.CatlogsId){
                  this.CatlogsPermissions.push(element);
                }
              }
            }
          }                    
          if (element.CatlogsType == 'PermissionsCRUD') {
            this.CatlogsPermissionsCRUD.push(element);
          }          
          
        }            
      });
    }

    logout() {
      this.authenticationService.logout();
      this.router.navigate(['/login']);
    }

    isExpanded = false;

    collapse() {
      this.isExpanded = false;
    }

    toggle() {
      this.isExpanded = !this.isExpanded;
    } 
    
}
