import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { BaseService } from './base.service';
import { map } from 'rxjs/operators';
import { AgencyModel } from '../_models/agency-model-data';

@Injectable({ providedIn: 'root' })
export class AgencyService extends BaseService {
  //private currentAgencySubject: BehaviorSubject<AgencyModel>;
  public currentAgency: Observable<AgencyModel>;
  constructor(private http: HttpClient) {
      super();
      //this.currentAgencySubject = new BehaviorSubject<AgencyModel>(JSON.parse(localStorage.getItem('currentUser')));
      //this.currentAgency = this.currentAgencySubject.asObservable();
      this.setController('Agency');
  }
  /*public get currentAgencyValue(): AgencyModel {
      return this.currentAgencySubject.value;
  }*/
  Save(Agency: any) {
    return this.http.post<any>(`${this.endPointValue()}`, Agency)
      .pipe(map(agency => {
        // store user details and jwt token in local storage to keep user logged in between page refreshes
        /*
        localStorage.setItem('currentUser', JSON.stringify(user));
        this.currentAgencySubject.next(agency);*/
        console.log(agency);
        return agency;
      }));
  }
  public getAgencyId = (id: any) => this.http.get(this.endPointValue(id));
  public getAgency = () => this.http.get(this.endPoint());
}
